@layer base {
  * {
    border-color: var(--border);
    outline-color: var(--ring);
    @supports (color: color-mix(in lab, red, red)) {
      outline-color: color-mix(in oklab, var(--ring) 50%, transparent);
    }
  }
  body {
    background-color: var(--background);
    color: var(--foreground);
  }
}
@layer utilities {
  @keyframes marquee {
    0% {
      transform: translateX(0%);
    }
    100% {
      transform: translateX(-50%);
    }
  }
  .animate-marquee {
    animation: marquee 30s linear infinite;
  }
  .bg-pattern-dots {
    background-image: radial-gradient(circle, rgba(255, 30, 86, 0.1) 1px, transparent 1px);
    background-size: 30px 30px;
  }
  .bg-pattern-lines {
    background-image: linear-gradient(90deg, rgba(57, 255, 20, 0.05) 1px, transparent 1px),
      linear-gradient(rgba(57, 255, 20, 0.05) 1px, transparent 1px);
    background-size: 40px 40px;
  }
  .bg-pattern-diagonal {
    background-image: repeating-linear-gradient(
      45deg,
      transparent,
      transparent 35px,
      rgba(255, 30, 86, 0.03) 35px,
      rgba(255, 30, 86, 0.03) 70px
    );
  }
  .bg-pattern-mesh {
    background-image: linear-gradient(0deg, rgba(57, 255, 20, 0.03) 1px, transparent 1px),
      linear-gradient(90deg, rgba(255, 30, 86, 0.03) 1px, transparent 1px);
    background-size: 50px 50px;
  }
  .bg-gradient-neon {
    background: linear-gradient(135deg, #1a237e 0%, #0a0e3f 50%, #1a237e 100%);
  }
  .bg-gradient-accent {
    background: linear-gradient(135deg, rgba(255, 30, 86, 0.1) 0%, rgba(57, 255, 20, 0.05) 100%);
  }
  .hero-cta {
    bottom: clamp(3.5rem, 12vw, 7.5rem);
  }
  @media (min-width: 768px) {
    .hero-cta {
      bottom: clamp(4.5rem, 10vw, 9rem);
    }
  }
  @media (min-width: 1280px) {
    .hero-cta {
      bottom: clamp(4rem, 7vw, 8rem);
    }
  }
}
